.frase{
  width: 100%;
  overflow: hidden;
  background-color: #492933;
  color: #FFE1D0;
  font-size: 18px;

  &__head{
    width: 100%;
    display: inline-block;
    text-align: center;
    margin-top: 50px;

      &__big{
        display: inline-block;
        text-align: center;
      }

      &__small{
        display: inline-block;
        font-size: 20px;
        line-height: 28px;
        // line-height: 46px;
        vertical-align: middle;
      }
  }

  &__ground{
    width: 100%;
    display: inline-block;
    text-align: center;
    margin-bottom: 70px;
  }
}